/**
* 
* */
export function loadTreeData(callback) {


    /**************
    * 
    *************/

    let requestParam = {
        pk_org: this.props.search.getSearchValByField("search", "pk_org").value.firstvalue,
        syscode: this.props.search.getSearchValByField("search", "syscode").value.firstvalue
    };
    /*************
    * ajax 
    *************/
    ajax({
        url: urlArray.loadTreeDataUrl,
        data: requestParam,
        success: (result) => {
            if (result.success) {
                this.root.refname = this.state.json['M0Z10207-000000']
                this.root.title = this.state.json['M0Z10207-000000']
                let data = [Object.assign({ ...this.root }, { children: result.data })];
                //  
                dealTreeData(data)
                this.props.syncTree.setSyncTreeData(treeId, data)
                //  
                this.props.syncTree.openNodeByPk(treeId, this.root.refpk);
                this.props.form.setFormStatus(formId, 'browse')
                updateStatus.call(this);
                callback && callback.call(this);
            }
        }
    });
}

