function doDel(selectedTreeNode) {
    if (!selectedTreeNode) return;
    let requestParam = {
        pk_chargetype: selectedTreeNode.refpk,
        ts: selectedTreeNode.nodeData.ts
    }
    ajax({
        url: urlArray.delTreeNodeUrl,
        data: requestParam,
        success: (result) => {
            if (selectedTreeNode.nodeData.isDown == 1) {
                toast({ content: this.state.json['M0Z10207-000030'], color: 'warning' });/*  ,*/
            } else {
                if (result.success) {
                    this.props.form.EmptyAllFormValue(formId);
                    //
                    this.props.asyncTree.delTreeData(treeId, selectedTreeNode.refpk);
                    toast({ content: this.state.json['M0Z10207-000018'], color: 'success' });/*  ,*/
                    updateStatus.call(this);
                    //
                    this.props.syncTree.delNodeSuceess(treeId, selectedTreeNode.refpk)
                    //
                    this.props.syncTree.openNodeByPk(treeId, selectedTreeNode.pid);
                    //
                    this.props.syncTree.setNodeSelected(treeId, selectedTreeNode.pid);
                    //
                    this.props.form.setFormStatus(formId, 'browse');
                }
            }
        }
    })
}

/**
* 
*/
function doCancel() {

    // 
    let selectedTreeNode = this.props.syncTree.getSelectNode(treeId);//
    if (selectedTreeNode == null) {
        this.props.form.EmptyAllFormValue(formId)
        this.props.form.setFormStatus(formId, 'browse')
        updateStatus.call(this);
        return
    }
    /**********************************************************
    *
    * 
    *      1
    *      2
    *      3
    *
    **********************************************************/

    /**********************************************************
    * modified by wangying16 on 2018/12/12 for NCCLOUD-95993
    * 1.ajax
    * 2.NC
    **********************************************************/
    if (selectedTreeNode && selectedTreeNode.refpk != '~') {
        loadFormData.call(this, selectedTreeNode.refpk, () => {
            this.props.form.setFormStatus(formId, 'browse')
            updateStatus.call(this);
        })
    } else {
        //  
        this.props.form.EmptyAllFormValue(formId);
        this.props.form.setFormStatus(formId, 'browse');
        //
        updateStatus.call(this);
    }
}



