//
export function beSureBtnClick(props) {
    if (this.state.pk_org == '') {
        toast({ color: 'warning', title: "" });/*  */
        return;
    }
    let dbinfo = this.state.dbinfo;
    if (Object.keys(dbinfo).length == 0) {
        toast({ color: 'warning', title: "!" });/*  */
        return;
    }
    ajax({
        url: urlArray.downUrl,
        data: {
            ref1: this.state.dbinfo,
        },
        async: false,
        success: (res) => {
            let { success, data } = res;
            //
            if (success) {
                toast({ color: 'success', title: data });/*  */
            } else {
                toast({ color: 'success', title: "" });
            }
            props.modal.close('selds')
            loadTreeData.call(this, () => {
                this.props.form.EmptyAllFormValue(formId);
                updateStatus.call(this);
            });
        }
    });


}

