import {loadFormData} from '../action/action';

var treeId = 'chargetypeTree';//Id
var formId = 'chargetype';//Id
/**
* 
* @param refpk
*/
export function onSelectTree(refpk) {

if (!refpk || refpk === null) return;

//    
if (this.props.form.getFormStatus(formId) === 'edit') return;

if (refpk === '~') {
//
this.props.form.EmptyAllFormValue(formId);
return;
}

loadFormData.bind(this)(refpk);
}

/**
* 
* @param key
*/
export function onMouseEnterEve(key) {
//
if (key === this.root.refpk) {
let obj = {
delIcon: false, //false: true:; true
editIcon: false,
addIcon: true
};
this.props.syncTree.hideIcon(treeId, key, obj);
}
}

/**
* 
* @param props
* @param moduleId
* @param key
* @param value
* @param index
*/
export function onAfterFormEvent(props, moduleId, key, value, index) {
 if(key==='syscode'){
     let sysValue = value.value;
      //
      let metas = props.meta.getMeta();
      for (var item of metas['chargetype'].items) {
          if (item.refcode) {
              if (item.attrcode === 'pk_sssrxm') { //
                  item.queryCondition = () => {
                      return {
                          pk_org: this.state.pk_org,
                          syscode : sysValue
                      };
                  };
              }

          }
      }
      //   
      props.form.setFormItemsValue(moduleId,{'pk_sssrxm':{value: null,display: null}});
      props.meta.setMeta(metas);
 }

}

/**
* 
* @param {*} props
* @param {*} moduleId
* @param {*} key
* @param {*} value
* @param {*} data
*/
export function onBeforeFormEvent(props, moduleId, key, value, data) {
if(key === 'pk_sssrxm'){
    let sysValue = data.syscode.value; 
    let metas = props.meta.getMeta();
    for (var item of metas['chargetype'].items) {
        if (item.refcode) {
            if (item.attrcode === 'pk_sssrxm') { //
                item.queryCondition = () => {
                    return {
                        pk_org: this.state.pk_org,
                        syscode : sysValue
                    };
                };
            }

        }
    }
    props.meta.setMeta(metas);
}

return true;
}
