const { Refer } = high;

export default function (props = {}) {
    let conf = {
        multiLang: {
            domainName: 'hpf',
            currentLocale: 'zh-CN',
            moduleId: 'M0Z10103',
        },
        isCacheable: false,
        refType: 'grid',
        refName: '',/*  */
        placeholder: '',/*  */
        refCode: 'hpf/refer/hpfrefs/HpfEntryConfigGridRef/index',//
        queryGridUrl: '/nccloud/hpf/entryconfig/GridReferQuery.do',
        columnConfig: [{ name: ['', ''], code: ['code', 'name'] }],/*  ,*/
        isMultiSelectedEnabled: false,
        isHasDisabledData: false
    };

    return <Refer {...Object.assign(conf, props)} />;
}
